import { tabsEmits } from 'element-plus';
import { createStore } from 'vuex'

export default createStore({
  state: {
    cacheSceneTableData: [],
    cacheCurTitleText: '',
    selectedKeys: ['1'],
    cacheSceneData: [],
    tabSelFlag: '',
    schemeTable: {},
    cacheTp: {},
    cacheAfterPredictDate: null,
    cacheAfterPredict: null,
    m4Data: {},
    cacheMergeColumn: {},
    cacheRate80: 40000,
    exportData: {},
    exporting: false
  },
  getters: {
    selectedKeys: state => state.selectedKeys,
    cacheSceneData: state => state.cacheSceneData,
    tabSelFlag: state => state.tabSelFlag,
    schemeTable: state => state.schemeTable,
    cacheTp: state => state.cacheTp,
    exportData: state => state.exportData,
  },
  mutations: {
    SET_SELECTED_KEYS(state, keys) {
      state.selectedKeys = keys;
    },
    SET_CACHE_SCENE_DATA(state, data) {
      state.cacheSceneData = data;
    },
    SET_TAB_SEL_FLAG(state, data) {
      state.tabSelFlag = data;
    },
    SET_SCHEME_TABLE(state, data) {
      const tabSel = data['tabSel']
      if (!Object.keys(state.schemeTable).includes(tabSel)) {
        state.schemeTable[tabSel] = data['data'];
      }
    },
    APPEND_SCHEME_TABLE(state, dataItem) {
      const data = dataItem['data']
      const time = data['time']
      const selTab = dataItem['selTab']
      const dataObj = state.schemeTable[selTab]
      const targetElement = dataObj.find(element => element.time === time);
      if (targetElement) {
        targetElement['arg1'] = data['arg1'];
        targetElement['arg2'] = data['arg2'];
      } else {
        state.schemeTable[selTab].push(data);
      }
      console.log('添加后缓存', state.schemeTable[selTab])
    },
    DEL_SCHEME(state, data) {
      state.schemeTable[data['tabSel']] = state.schemeTable[data['tabSel']].filter((item) => item['time'] !== data['time'])
    },
    CACHE_TP(state, data) {
      state.cacheTp[data.scene] = data.tp;
    },
    CACHE_RATE80(state, data) {
      state.cacheRate80 = data
    },
    UPDATE_SINGLE_SCHEME(state, data) {
      const time = data['time']
      const newName = data['uptName']
      const old = state.schemeTable[data['tabSel']]

      old.forEach(i => {
        if (i['time'] === time) {
          const arg2Data = i['arg2']['data']
          let sumArg2 = 0
          arg2Data.forEach(j => {
            if (j['label'] === data['name']) {
              j['val'] = data['val']
              j['label'] = newName
            }
            sumArg2 += j['val']
          })
          i['arg1']['vals'][1] = parseFloat(sumArg2).toFixed(2)

          // 根据 label 中的 "一般调峰"、"深度调峰"、"应急保障" 顺序进行排序
          const order = ["一般调峰", "深度调峰", "应急保障"];
          arg2Data.sort((a, b) => {
            const aLevel = order.findIndex(level => a['label'].includes(level));
            const bLevel = order.findIndex(level => b['label'].includes(level));
            return aLevel - bLevel;
          });

        }
      });
    },
    setCacheAfterPredictDate(state, date) {
      state.cacheAfterPredictDate = date;
    },
    setCacheAfterPredict(state, data) {
      state.cacheAfterPredict = data;
    },
    cacheSceneTableData(state, data) {
      state.cacheSceneTableData = data;
    },
    cacheMergeColumn(state, data) {
      state.cacheMergeColumn = data;
    },
    cacheCurTitleText(state, data) {
      state.cacheCurTitleText = data;
    },
    cacheM4Data(state, data) {
      state.m4Data = data;
    },
    setShowTkSchemeTable(state, data) {
      const tabSel = data['tabSel']
      const item = state.schemeTable[tabSel]
      item['showTkSchemeTable'] = data['val']
    },
    setTkSchemeImage(state, data) {
      const tabSel = data['tabSel']
      const item = state.schemeTable[tabSel]
      item['image'] = data['val']
    },
    clearTk(state, data){
      state.cacheTp = {}
      state.schemeTable = {}
      state.tabSelFlag = {}
    },
    updateExportData(state, data){
      state.exportData[data['key']] = data['value'];
    },
    setExporting(state, data){
      state.exporting = data
    }
  },
  actions: {
    updateSelectedKeys({ commit }, keys) {
      commit('SET_SELECTED_KEYS', keys);
    },
    updateCacheSceneData({ commit }, data) {
      commit('SET_CACHE_SCENE_DATA', data);
    },
    updateTabSelFlag({ commit }, data) {
      commit('SET_TAB_SEL_FLAG', data);
    },
    updateSchemeTable({ commit }, data) {
      commit('SET_SCHEME_TABLE', data);
    },
    appendSchemeTable({ commit }, data) {
      commit('APPEND_SCHEME_TABLE', data)
    },
    delScheme({ commit }, data) {
      commit('DEL_SCHEME', data)
    },
    cacheTp({ commit }, data) {
      commit('CACHE_TP', data)
    },
    updateSingleScheme({ commit }, data) {
      commit('UPDATE_SINGLE_SCHEME', data)
    }
  },
  modules: {
  }
})
